import React from 'react';
import Book from './Book';
import PropTypes from 'prop-types';
import createClass from 'create-react-class';

class Form extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      count: Date.now(),
      results: [],
      city: ""
    };
  }

  render() {
    return (
      <div className="form">
        <br />
        <input type="text" placeholder="Type city name, like ottawa..." ref="search" onChange = {this.handleCityChange} /><br />

        <button onClick={this.handleButtonClick}>Search by city</button><br />
        {this.state.city}   <br />        
        {this.state.results.map((rest,idx) => { return (<div key={idx}> {idx+1}| {rest.name} |{rest.address}|{rest.price}</div>) })}

      </div>
    )
  }

  handleButtonClick = () => {
    event.preventDefault();
    let searchKey = this.refs.search.value;
    searchKey = searchKey.trim();
    searchKey = searchKey.toLowerCase();
    console.log(this.refs.search.value);
    let searchResult = this.searchRestaurantByCity(searchKey);
  }


  searchRestaurantByCity(searchKey) {
    let _this = this;

    fetch('http://opentable.herokuapp.com/api/restaurants?city=toronto')
    .then((resp) => resp.json()
    )
    .then(function(data) {
      let ppp = data.restaurants;  
      let ids = ppp.map(re=>{ return {id:re.id}})
      console.log(ids) 

         var tmp = {data:data};
         let totalEntries = data.total_entries;
         let pageEntries = 100;        
         var city = searchKey;
         var pageCount = Math.ceil(totalEntries/pageEntries);
     
         var pArray = [];
         for (var i=0; i< pageCount; i++) {
            var pagenumber = i+1;
            var pagesize = 100;
            var url = `http://opentable.herokuapp.com/api/restaurants?city=${city}&per_page=${pagesize}&page=${pagenumber}`
            console.log(url);
           var p = fetch(url)
             .then((resp) => resp.json())
             .then((data) => data.restaurants) 
                  
            pArray.push(p);
         }
    
    Promise.all(pArray).then(function(p){
      var a = [];
      for (var i =0;i < p.length;i++) {
        var b = p[i]; 
        for (var j =0;j < b.length;j++) {
              var c =  b[j];
               a.push(
                 {
                   id:c.id,
                   name:c.name,
                   address:c.address,
                   price:c.price 
                 }
              );
        }
      } 
         _this.setState({
           count:totalEntries,
           results:a
        });     

      console.log(JSON.stringify(a[0],null,2));
    }, function(err){
      console.log(err);
    });
     console.log("------page count----" + pageCount) ;
     })
    .catch(function(err) {
       console.log('Fetch Error :-S', err);
    });
  }  

  searchRestaurantByCity01(city) {
    let _this = this;
    let searchResults = [{id:4},{id:5}]; 

    var p1 = new Promise(function(resolve, reject){
      setTimeout(function(){
        resolve([{id:4},{id:11}]);
      }, 1000);
    });
    
    var p2 = new Promise(function(resolve, reject){
      setTimeout(function(){
        resolve([{id:14},{id:21}]);
      }, 2000);
    });

    var p3 = new Promise(function(resolve, reject){
      setTimeout(function(){
        resolve([{id:24},{id:31}]);
      }, 2000);
    });
    
    var pArray = [p1, p2, p3];
    
    Promise.all(pArray).then(function(p){
      var a = [];
      for (var i =0;i < p.length;i++) {
        var b = p[i]; 
        for (var j =0;j < b.length;j++) {
              var c =  b[j];
               a.push(c);
        }
      } 
      console.log(a); 
      _this.setState({results:a}) 
    }, function(err){
      console.log(err);
    });

    return searchResults ;
  }


  handleTitleChange = (event) => {
    event.preventDefault();
    console.log(event.target.value);
    this.setState({ city: event.target.value })
  }

}

export default Form;